.todo-list {
  font-size: 25px;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-top: 1rem;
}

.todo-list input {
  margin-right: 2rem;
  margin: 1rem;
}

.display-box {
  display: flex;
  justify-content: space-around;
  border: none;
  font-size: 1.2rem;
  margin: 1rem;
}

.radio--btn {
  accent-color: #b262ff;
}

.radio--btn--dark {
  accent-color: black;
}
